import { AbstractControl, Validators, ValidatorFn } from '@angular/forms';

import { isPresent, isDate } from '../util/lang';

export const /** @type {?} */ maxDate = (val: any | AbstractControl): ValidatorFn => {
    let /** @type {?} */ subscribe = false;
    let /** @type {?} */ otherValue = val;
    const /** @type {?} */ subscribable = val instanceof AbstractControl;
    if (!subscribable && !isDate(val) && !(val instanceof Function)) {
        throw Error('maxDate value must be or return a formatted date');
    }

    return (control: AbstractControl): { [key: string]: any } => {
        if (subscribable && !subscribe) {
            subscribe = true;
            ( /** @type {?} */((val as AbstractControl))).valueChanges.subscribe(() => {
                control.updateValueAndValidity();
            });
        }
        if (isPresent(Validators.required(control))) {
            return null;
        }

        let /** @type {?} */ d: Date = new Date(control.value);

        if (!isDate(d)) {
            return {
                minDate: true
            };
        }
        if (val instanceof Function) {
            otherValue = val();
        }
        if (subscribable) {
            otherValue = val.value;
        }
        return d <= new Date(otherValue) ? null : { maxDate: true };
    };
};
