import { AbstractControl, ValidatorFn } from '@angular/forms';

export const /** @type {?} */ notEqualTo = (notEqualControl: AbstractControl): ValidatorFn => {
    let /** @type {?} */ subscribe = false;
    return (control: AbstractControl): { [key: string]: boolean } => {
        if (!subscribe) {
            subscribe = true;
            notEqualControl.valueChanges.subscribe(() => {
                control.updateValueAndValidity();
            });
        }

        let /** @type {?} */ v: string = control.value;

        return notEqualControl.value !== v ? null : { notEqualTo: true };
    };
};
